  import ssSelectCity from '@/components/ss-select-city/ss-select-city.vue'
  export default {
	  components: {
	    ssSelectCity
	  },
    data() {
      return {
        hotCitys: ['周口'],
        value: ''
      }
    },
	
	onLoad(options) {
		this.value = options.city
	},
    
    methods: {
      onSelect(city) {
		// 把所选的城市放在全局变量里
		getApp().globalData.city = city
		uni.navigateBack({
		    delta: 1
		})
      }
    }
  }
